import { Component, OnInit } from '@angular/core';
import * as dataFooter1 from './globalComponents/jsonFiles/footer.json';
import { FooterV1 } from './globalComponents/footer/footer_v1/footer_v1.component';
import { MenuIteam } from './Models/menu/menu.module';
import { HttpClient } from '@angular/common/http';
import * as dataMenu from './globalComponents/jsonFiles/menu.json';
import { StaticService } from './services/static/static.service';
import { Observable } from 'rxjs';

@Component({
  selector: '.app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  public menuIteams: MenuIteam[] = [];
  public addmenuIteam: MenuIteam = new MenuIteam();
  public modifymenuIteam: MenuIteam = new MenuIteam();
  public indexToModify;
  public templateNames: any = [];
  public selectedTemplate;

  //link of the host 
  public host: string = "http://localhost:3000";
  // constractor
  constructor(private staticServ: StaticService, public footer: FooterV1, private httpClient: HttpClient) { }

  dataFooter: any = (dataFooter1 as any).default;

  ngOnInit() {
    this.loading = null;
    this.loading2 = null

    // localStorage.setItem("paramsLoading" , "false");

    if (localStorage.getItem("paramsLoading") == "true") {
      document.getElementById("_parmsID").style.display = "block";
      this.displayProgressPub();
    }
    // will read all menuIteam from menu.json
    dataMenu.menuIteam.forEach(element => {
      this.menuIteams.push(element);
    });

    this.getTemplateName();
    this.footer.getData(this.dataFooter);
  }
  
  // will get all template from folder pages
  getTemplateName() {
    this.httpClient.get('http://localhost:3000/api/templateList').subscribe(
      (data) => {
        for (let index = 0; index < data["reponse"].length; index++) {
          this.templateNames.push(data["reponse"][index]);
        }
        console.log(this.templateNames);
      }
    );
  }
  public errMessage: String = null;
  public loading: String = null;
  public loading2: String = null;

  //will show and hide menu iteam
  hideShowMenuIteam(indexMenu) {
    var Indata = { 'indexMenuIteam': indexMenu };
    this.loading2 = "loading...";
    this.httpClient.post('http://localhost:3000/api/hideShow', Indata).subscribe();
  }

  //will add new page
  addNewPage() {
    this.addmenuIteam.activationMenuIteam = true;
    if (this.addmenuIteam.nameMenuIteam && this.addmenuIteam.routeMenuIteam && this.addmenuIteam.nameComponant) {
      this.errMessage = null;
      this.loading = "loading...";
      var Indata = {
        'compoName': this.addmenuIteam.nameMenuIteam,
        'nameTemplate': this.addmenuIteam.nameComponant,
        'linkTemplate': this.addmenuIteam.routeMenuIteam,
      };
      console.log(this.addmenuIteam);
      this.httpClient.post('http://localhost:3000/api/creatCompo', Indata).subscribe(
        () => {
          console.log("create with success")
        },
        (err) => {
          this.errMessage = "faild to create componant" + err;
          this.loading = null;
        }
      );
    } else {
      this.errMessage = "message error: please 3emer kolshi";
    }
  }
  //will delete page
  deletPage(index) {
    var Indata = { 'indexMenuIteam': index };
    this.loading2 = "loading...";
    this.httpClient.post('http://localhost:3000/api/deletCom', Indata).subscribe(
      () => {
        console.log("delet with success")
      }
    );
  }
  //will send value to the pop up
  affectValueToPopUp(index) {
    this.indexToModify = index;
    this.modifymenuIteam.nameMenuIteam = this.menuIteams[index].nameMenuIteam;
    this.modifymenuIteam.routeMenuIteam = this.menuIteams[index].routeMenuIteam;
    this.openModalupdate();
  }
  //will update name and link of menu Iteam
  updatePage() {
    this.loading = "loading...";
    var Indata = {
      'indexMenuIteam': this.indexToModify,
      'nameMenuIteam': this.modifymenuIteam.nameMenuIteam,
      'routeMenuIteam': this.modifymenuIteam.routeMenuIteam
    };
    console.log(Indata);
    this.httpClient.post('http://localhost:3000/api/updateMenuIteam', Indata).subscribe();
  }

  //lets generate static content X-)
  generateStaticContent() {
    document.getElementById("loaderDiv").style.display = "block";
    this.staticServ.generate().subscribe((data) => {
      console.log(data);

      if (data['status'] == true) {
        document.getElementById("loaderDiv").style.display = "none";
        document.getElementById("css-modal").style.display = "block";
      } else {
        document.getElementById("loaderDiv").style.display = "none";
      }

    }, err => {
      console.log(err);
    });
  }
  
  displayProgressPub(): Observable<any> {

    localStorage.setItem("paramsLoading", "true");
    document.getElementById("_parmsID").style.display = "block";

    this.staticServ.generate().subscribe((data) => {
      console.log(data);

      if (data['status'] == true) {

        document.getElementById("loaderStatic").className = '';
        document.getElementById("loaderStatic").innerHTML = `<i class="fas fa-check"></i>`
        document.getElementById("loaderServer").className = 'spinner-border spinner-border-sm';
        document.getElementById("loaderServer").innerHTML = ``;
        localStorage.setItem("paramsLoading", "false");

        this.staticServ.publish().subscribe((data) => {
          console.log(data);
          if (data['status'] == true) {
            document.getElementById("loaderServer").className = '';
            document.getElementById("loaderServer").innerHTML = `<i class="fas fa-check"></i>`
          }
        }, err => {
          console.log(err);
        });
      } else {
        alert("HOHOoo");
      }

    }, err => {
      console.log(err);
    });
    return;
  }
  
  onActivate(event) {
    window.scroll(0, 0)
  }
  
  openModal() {
    document.getElementById("adding").style.display = "table";
  }
  
  closeModal() {
    document.getElementById("adding").style.display = "none";
  }
  
  openModalupdate() {
    document.getElementById("updating").style.display = "table";
  }
  
  closeModalupdate() {
    document.getElementById("updating").style.display = "none";
  }
  
  closeAlertMessage() {
    document.getElementById("css-modal").style.display = "none";
  }

}
